.App {
  text-align: left;
  max-width: 700px;
  padding-left:1em;
  padding-right:1em;
  font-family: 'Noto Snas KR';
  font-size: 11pt;
  margin:auto;
}
.profile-img-box{
  display: flex;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 1px solid black;
  overflow: hidden;
  box-sizing: border-box;
  margin-left: 0.4em;
}
.profile-img{
  width:100%;
  height:100%;
  object-fit: cover;
}
@media (max-width: 767px){
  .profile-img-box{
    margin: auto;
  }
  .clearfix{
    text-align: center;
    display: table;
  }
}
@media (min-width: 768px) {
  .profile-img-box{
    float: right;
  }
  .clearfix::after{
    content: "";
    clear: both;
    display: table;
  }
}

.pub-title{
  padding-left: 0.5em;
  border-left: 3.5px solid #616161;
}

.svg-inline{
  overflow: visible;
  display: inline-block;
  font-size: inherit;
  height: 1em;
  overflow: visible;
  vertical-align: -0.125em;
}
.logo_size{
  width: 15px;
}
.logo_hsize{
  padding-right: 0.3em;
}

a {
  background-color: initial;
  color: #007bff;
  text-decoration: none;
}
.Btns{ /*모바일에서 좌우 flex, 텍스트중앙정렬*/
  display: flex;
  text-align: center;
}
.btn {
  overflow: hidden;
  padding: 8px 10px;
  margin: 15px 5px 15px 5px;
  display: inline-block;
  font-size: 14px;
  outline: none;
  color: #fff;
  border-radius: 6px;
  font-weight: bold;
  flex: 0 1 auto;
}
.btn:hover{
  cursor: pointer;
  transition: background-color 0.5s, color 0.5s;
}
.btnRed {
  background: #fff;
  border: 1.5px solid #ef4836;
  color: #ef4836;
  &:hover{
    background: #ef4836;
    border-color: #ef4836;
    color: #fff;
  }
  &:active{
    background: #ef4836;
    border-color: #fff;
  }
}
