/* 사이드바 시작 */

/* 사이드바의 너비와 높이를 변수를 통해 통제 */
:root {
  --side-bar-width: 200px;
  --side-bar-height: 90vh;
}

.sideBar {
  position: fixed;    /* 스크롤을 따라오도록 지정 */
  width: 50px;
  height: 55px;
  margin-top: 1vh;    /* 사이드바 위와 아래의 마진을 동일하게 지정 */
  border-radius: 10px;
  transition: .5s;
  background-color: black;
}
.sideBar:hover {
  width: var(--side-bar-width);
  min-height: 423px;
  max-height: 90vh;
  opacity:1;
}
@media (min-width: 768px) {
  .sideBar{
    opacity:0.5;
    transform: translate(-70px, 0);
  }
  .sideBar:hover{
    opacity:0.8;
    transform: translate(-70px, 0);
  }
}
@media (max-width: 767px) { /*mobile*/
  .sideBar{
    opacity:0.2;
  }
  .sideBar:hover{
    opacity:0.8;
    transform: translate(-20px, 0);   /* 둥근 모서리의 너비만큼 숨겨주기 */
  }
}
.sideBar > ul ul {
  display: none;
}
.sideBar > ul > li {
  display: none;
}
.sideBar:hover > ul > li {
  display: block;
}


/* 아이콘 시작 */
.sideBar__iconBox {
  display: flex;
  justify-content: flex-end;
}

.sideBar__icon1 {
  position: relative;
  width: 23px;
  height: 17px;
  margin: 15px;
  margin-top: 20px;
  transition: .5s;
}

:root {
  --side-bar__icon: .5s;
}

.sideBar__icon1 > div {
  position: absolute;
  width: 100%;
  height: 20%;
  background-color: white;
  transition: all var(--side-bar__icon);
}

.sideBar__icon1 > div:nth-of-type(1) {
  top: 0;
  width: auto;
  left: 0;
  right: 0;
  transition: all var(--side-bar__icon), left calc(var(--side-bar__icon) / 2) calc(var(--side-bar__icon) / 2), right calc(var(--side-bar__icon) / 2) calc(var(--side-bar__icon) / 2), height calc(var(--side-bar__icon) / 2) 0s;
}

.sideBar__icon1 > div:nth-of-type(2) {
  top: 40%;
  transform-origin:bottom left;
}

.sideBar__icon1 > div:nth-of-type(3) {
  top: 80%;
  left: auto;
  right: 0;
  transform-origin:bottom right;
}


.sideBar:hover .sideBar__icon1 {
  transform: translate(-148px, 0);
}

.sideBar:hover .sideBar__icon1 > div:nth-of-type(2) {
  transform:rotate(45deg);
  width: 70.5%;
  height: 25%;
}

.sideBar:hover .sideBar__icon1 > div:nth-of-type(3) {
  top: 40%;
  transform:rotate(-45deg);
  width: 70.5%;
  height: 25%;
}

.sideBar:hover .sideBar__icon1 > div:nth-of-type(1) {
  left: 41%;
  right: 41%;
  height: 100%;
  transition: all var(--side-bar__icon), left calc(var(--side-bar__icon) / 2) 0s, right calc(var(--side-bar__icon) / 2) 0s, height calc(var(--side-bar__icon) / 2) calc(var(--side-bar__icon) / 2);
}
/* 아이콘 끝 */

/* 모든 메뉴의 a에 속성값 부여 */
.sideBar ul > li > a {
  display: block;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  padding-top: 0.7rem;
  padding-bottom: 0.7rem;
  padding-left: 1rem;
  padding-right: 1rem;
  transition: .5s;
}

/* 자식의 position이 absolute일 때 자식을 영역 안에 가두어 준다 */
.sideBar > ul > li {
  position: relative;
}

/* 모든 메뉴가 마우스 인식 시 반응 */
.sideBar ul > li:hover > a {
  background-color: #555;
  border-bottom: 1px solid #999;
  border-radius: 10px 10px 10px 10px;
}
/*2차 메뉴 있을 때 우측 모서리 직각*/
.sideBar ul > li:has(>ul):hover > a{
  border-radius: 10px 00px 0px 10px;
}

/* 1차 메뉴의 항목이 마우스 인식 시에 2차 메뉴 등장 */
.sideBar > ul > li:hover > ul {
  display: block;
  position: absolute;
  background-color: #888;
  top: 0;         /* 2차 메뉴의 상단을 1차 메뉴의 상단에 고정 */
  left: 100%;     /* 2차 메뉴를 1차 메뉴의 너비만큼 이동 */
  width: 100%;    /* 1차 메뉴의 너비를 상속 */
  border-radius: 0px 10px 10px 0px;
}
.sideBar > ul ul > li > a {
  border-radius: 0px 10px 10px 0px;
}
.sideBar > ul ul > li:hover > a {
  border-radius: 0px 10px 10px 0px;
}

/* 사이드바 끝 */

